
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}




















#projects{
  margin: 100px;
}

.project{
  padding: 100px 50px;
}
.image{
  float: left;
  width: 200px;
  height: 200px;
  overflow: hidden;
  position: relative;
}
img{
  position: absolute;
  top: -9999px;
  left: -9999px;
  right: -9999px;
  bottom: -9999px;
  margin: auto;
}
